@import "font-face.scss";



* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
    font-family: creato-reg, serif;
    // font-family: bebas-reg, serif;
    font-size: 18px;
    line-height: 200%;
}


@mixin df {
    display: flex;
}

@mixin df-jcc {
    display: flex;
    justify-content: center;
}

@mixin df-aic {
    display: flex;
    align-items: center;
}

@mixin df-jcc-aic {
    display: flex;
    justify-content: center;
    align-items: center;
}


a {
    text-decoration: none;
    color: unset;
}


.dG {
    display: grid;
}


.df {display: flex;}
.jcc {justify-content: center;}
.jcsb {justify-content: space-between;}
.jcsa {justify-content: space-around;}
.jcfs {justify-content: flex-start;}
.jcfe {justify-content: flex-end;}
.aic {align-items: center;}


.pr {position: relative;}
.pa {position: absolute;}



// $baseColor: #ffffff;
$baseColor: #fffdf7;
// $headerColor: #ffead0d4;
$headerColor: #fff4e7;

$headerHeight : 80px;


$font: 'creato_reg';



body {
// overflow-y: hidden;
background-color: $baseColor;
}

html {
    height: 100%;
}

// ___________________Header_________________________
// XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX

header {
    height: 100px;
    z-index: 10;
}

.headerOuterWrapper {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: $headerColor;
    border-bottom: 2px solid black;
    z-index: 10;
    height: $headerHeight;
}

.headerWrapper {

    max-width: 1000px;
    width: 100%;
    
    margin: 0 auto; 


}

.headerRight {
    display: flex; 
    justify-content: space-between; 
    min-width: 500px;
}

.headerElement {
    cursor: pointer;
}

.headerElement:hover {
    text-decoration: underline;
}

.logo {
    min-width: 300px;
    height: 80px;
}

.logo img {
    height: 100%;
}

.subMenuWrapper {
    max-width: 1000px;
    margin: 0 auto;
}

.subMenu {
    // display: none;

    position: fixed;
    top: $headerHeight;
    height: 0;
    overflow: hidden;

    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    background-color: $baseColor;

    transition: all 200ms ease-in;

    z-index: 1;
}

.subMenu.collapse {
    height: calc($headerHeight/2);

}

.subMenuItem:hover {
    text-decoration: underline;
}

.subMenuItem {
    margin: 20px;
}



// ___________________Pages_________________________
// XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX

.index, .bett {
    position: absolute;
    // top: 10%;

    height: 82vh;

    width: 100%;
}



// ___________________Landing_________________________
// XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX

.generalContainer {
    position: relative;

    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding-bottom: 300px;

    // height: calc(100vh - $headerHeight);


    // margin-top: $headerHeight;

    background-color: $baseColor;

    // overflow: scroll;
    // scroll-snap-type: y mandatory;
}

.maxWidth {
    max-width: 1000px;
    // margin: $headerHeight auto 0;
    margin: 0 auto 0;
    padding-top: 50px;

    
}

.snapContainer {

    // height: calc(100% - $headerHeight);
    height: calc(100vh - $headerHeight);
    overflow: scroll;

    // scroll-snap-type: y mandatory;

}



