.textBlock {
    margin-top: 100px;
    min-height: 500px;
    // scroll-snap-align: start;

}

h1 {
    // font-family: creato-bold, sans-serif;
    font-family: bebas-bold, sans-serif;
    font-size: 64px;
    margin-bottom: 50px;
}

h2 {
    font-family: bebas-bold, sans-serif;
    font-size: 48px;
    margin-bottom: 50px;  
}


.imgContainer {
    margin-top: 100px;
    margin-bottom: 10px;

    @include df;
    width: 100%;

    .imgLeft {
        width: 50%;

        @include df-jcc;
    }

    .imgRight {
        width: 50%;
        transform: translateY(50px);

        @include df-jcc;

    }

    img {

        max-width: 400px;

        transform: scale(1);
        transition: all 200ms ease;

        // box-shadow: 0 0 8px 8px white inset;

    }

    img:hover {
        transform: scale(1.1);


    }

}




